export const orderListMyReducer = (state = { orders: [] },action) => {
    switch (action.type) {
        case ORDER_LIST_MY_REQUEST:
            return {
                loading: true
            };

        case ORDER_LIST_MY_SUCCESS:
            return {
                loading: false,
                orders: action.payload //this is the orders that are thrown in from the action
            };

        case ORDER_LIST_MY_FAIL:
            return {
                loading: false,
                error: action.payload,
            };

        case ORDER_LIST_MY_RESET:
            return { orders: [] }

        //when user logout , make sure their orders are logout
        default:
            return state

    }
};
export const orderListReducer = (state = { orders: [] },action) => {
    switch (action.type) {
        case ORDER_LIST_REQUEST:
            return {
                loading: true
            };

        case ORDER_LIST_SUCCESS:
            return {
                loading: false,
                orders: action.payload //this is the orders that are thrown in from the action
            };

        case ORDER_LIST_FAIL:
            return {
                loading: false,
                error: action.payload,
            };

        default:
            return state

    }
};